.App {
  /* Text */
  text-align: center;
  color: white;
  font-family: "Courier New", Courier, monospace;

  /* Backgroun Image */
  background-image: url(./ny_skyline.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  /* Full height */
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.Header {
  padding-top: 200px;
}

/* Link states */
a {
  color: white;
  text-decoration: none;
}

a:visited {
  color: white;
}

a:hover {
  color: red;
}

a:active {
  color: white;
}
